export const getApiResource = async (url) => {
  try {
    const res = await fetch(url)
    
    if (!res.ok) {
      console.error('network fetch error:', res.status)
      return false
    }

    return await res.json()

  } catch (error) {
    console.error('network fetch error:', error.message)
    return false
  }
}

